import React from "react";
import { Link } from "react-router-dom";

function Navbar() {

    const handleScrollHome = () =>{
        window.scrollBy({ top: -9000, behavior: 'smooth' });
    }

    const handleScrollProject = () =>{
        window.scrollBy({ top: 500, behavior: 'smooth' });
    }

    const handleScrollContacts = () =>{
        window.scrollBy({ top: 9000, behavior: 'smooth' });
    }

    return(
        <div className="flex justify-center items-center backdrop-blur-3xl p-4 pt-10">
            <ul className="flex flex-row gap-5 md:gap-10">
                <li className="">
                    <Link onClick={handleScrollHome} className="link">Home</Link>
                </li>
                <li className="">
                    <Link onClick={handleScrollProject} className="link">Projects</Link>
                </li>
                <li className="">
                    <Link onClick={handleScrollContacts} className="link">Contacts</Link>
                </li>
            </ul>
        </div>
    ); 
}

export default Navbar;